import React,{useState} from "react";
import { useNavigate } from "react-router-dom";

import {Box,AppBar,Toolbar,Button,Typography,Tabs,Tab} from "@mui/material";
import { Link } from "react-router-dom";
import {useSelector,useDispatch} from 'react-redux'
import {authActions} from "../redux/store"
const Header = ()=>{
    //global state
    const isLogin=useSelector(state => state.isLogin)
    const dispatch = useDispatch();
    const navigate = useNavigate();
    
    //state
    const [value,setValue]=useState()

    //logout
    
    const handleLogout =()=>{
        try{
            dispatch(authActions.logout())
            alert('Logout Successfull')
            navigate('/login')

        }catch(error){
            console.log(error)

        }
    }
    return(
        <>
        <AppBar position = "sticky">
            <Toolbar>
                <Typography variant="h4">DogMatch App</Typography>  
                {isLogin && (
                      <Box display={'flex'} marginLeft="auto" marginRight={'auto'}>
                      <Tabs textColor="inherit" value={value} onChange={(e,val)=>setValue(val)}>
                          <Tab label = "Dogmatchrandom" LinkComponent={Link} to ="/Dogmatchrandom"/>
                          <Tab label = "Dogmatchcompare" LinkComponent={Link} to ="/Dogmatchcompare"/>
  
                      </Tabs>
                      
                      </Box>  
                )} 
                  
                <Box display={"flex"} marginLeft="auto">
                {!isLogin &&(    <> 
                    <Button sx={{margin:1,color:"white"}} LinkComponent={Link} to="/Home">Home</Button>
                <Button sx={{margin:1,color:"white"}} LinkComponent={Link} to="/login">Login</Button>
                    <Button sx={{margin:1,color:"white"}}LinkComponent={Link} to="/register">Register</Button>
                    </>
                    )}

                    {isLogin && (<Button onClick={handleLogout} sx={{margin:1,color:"white"}}>Logout</Button>)}
                    
                    </Box>   
                </Toolbar>
        </AppBar>
        </>
    )
}

export default Header;