import React, { useEffect, useState }  from 'react'
import {Form} from "react-bootstrap"
import Axios from 'axios'
import {CircularProgress} from '@mui/material'


const Dogmatchcompare = () =>{
    const [breed,setBreed] = useState([])
    const [selectedBreed,setSelectedBreed] = useState(" ")
    const [images,setImages]= useState([])

    useEffect(()=>{
        var URL = "https://dog.ceo/api/breeds/list/all";
        Axios 
        .get(URL)
        .then(res=>{
            for(var a in res.data.message){
                if(res.data.message[a].length !=0)
                for(var b of res.data.message[a])
                breed.push(a + "/" +b)
            else
            breed.push(a)
                setBreed(prev => [...breed])
                
            }
        }).then((r)=>{
            setSelectedBreed(breed[0])
            loadImages(breed[0]);
        })
       
    },[])
    function loadImages(category){
        var URL = `https://dog.ceo/api/breed/${category}/images`;
        Axios 
        .get(URL)
        .then(res => {
            
          
                setImages(prev => [...res.data.message])
                
            
           
        })
       


    }
    return( 
        <div className="app">
            
            <Form>
                <Form.Group>
                    <Form.Control as = "select" onChange={
                        (e)=>{
                            setSelectedBreed(e.target.value);
                            setImages([])
                            loadImages(selectedBreed)
                        }
                    }>
                        {
                            breed.map(b=>{
                                return <option key = {b}>{b}</option>
                            })
                        }
                      
                    </Form.Control>
                </Form.Group>
            </Form>
            {images.length==0?(<div className="center"><CircularProgress/></div>):
            <div className="image-box">
                {
                    images.map(i=>{
                        
                        return( 
                        <img src = {i}/>
                        )
                        
                    })
                }

            </div>
}
        </div>
    
    
    )
}

export default Dogmatchcompare;